#headerWrap {
    height: 160px;
    width: 900px;
    margin: 0 auto;
    border: 1px solid #737373;
    -webkit-box-shadow: 3px 3px 5px 1px #bfbfbf;
            box-shadow: 3px 3px 5px 1px #bfbfbf;
    font-size: 23px;
}

#date {
    text-align: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    padding-top: 20px;
    padding-bottom: 15px;
    background-color: #ebebeb;
    border-bottom: 1px solid #bfbfbf;
}

#backDay {
    float: left;
    margin-left: 50px;
}

#forwardDay {
    float: right;
    margin-right: 50px;
}

#DiaryHeader a {
    cursor: pointer;
}

#DiaryHeader .buttonDiv {
    display: inline-block;
    height: 140px;
    width: 300px;
    text-align: center;
}

#infoWrap h1 {
    font-size: 35px;
    margin-bottom: 5px;
    margin-top: 9px;
    padding: 0;
}

#infoWrap h2 {
    text-align: center;
    font-size: 23px;
    margin-top: 10px;
}

#caloriesWrap {
    color: #ff8000;
}

#carbonWrap {
    color: #404040;
}

#waterWrap {
    color: #4dffff;
}

#notLogged {
    color: #616161;
    text-align: center;
    padding-top: 40px;
}



