#DiaryPopUp {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
}

#popUpWrap {
    border: 1px solid #737373;
    -webkit-box-shadow: 3px 3px 5px 1px #bfbfbf;
            box-shadow: 3px 3px 5px 1px #bfbfbf;
    z-index: 10;
    height: 600px;
    width: 600px;
    margin: 0 auto;
    background-color: white;
}

#closeIconWrap {
    padding: 1px 4px;
    background: -webkit-gradient(linear, left top, left bottom, from(#ff3019),to(#cf0404));
    background: -webkit-linear-gradient(top, #ff3019 0%,#cf0404 100%);
    background: -o-linear-gradient(top, #ff3019 0%,#cf0404 100%);
    background: linear-gradient(to bottom, #ff3019 0%,#cf0404 100%);
    width: 9px;
}

#closePopUp {
    float: right;
    margin-right: 10px;
    margin-top: 10px;
    font-size: 12px;
    color: white;
    border: 1px solid #800000;
    cursor: pointer;
}

#popUpWrap #errorMessage {
    text-align: center;
}


