.navBar {
    background-color: #4dff4d;
    width: 100%;
    height: 70px;
    border-bottom-style: solid;
    border-bottom-width: 2px;
    border-bottom-color: #a9d9a9;
    position: fixed;
    top: 0;
    left: 0;

}

.logo {
    float: left;
    margin-left: 50px;
    margin-top: 11px;
    font-family: Roboto;
    font-size: 40px;
}

.buttonsWrap {
    float: right;
    margin-right: 35px;
    padding: 0 10px;
}


.navBar ul {
    list-style: none;
    margin-top: 9px;
}

.navBar li {
    display: inline-block;
    margin-right: 50px;
    height: 35px;
    border: 3px solid white;
    border-radius: 15px;
    color: white;
    font-weight: bold;
    font-size: 20px;
    padding: 0 20px;
    padding-top: 12px;
}

#profileName {
    color: white;
    border: none;
    font-size: 23px;
    font-weight: bold;
    margin-right: 50px;
}

.navBar a {
    text-decoration: none;
    color: white;
    cursor: pointer;
}

#hidden {
    display: none;
    border: none;
}



