#DiaryBody {
    width: 900px;
    margin: 0 auto;
    padding-bottom: 20px;
    font-size: 23px;
    margin-top: 30px;
}

#addFoodWrap {
    text-align: right;
}

#addFoodWrap i{
    margin-right: 20px;
}

#addFood {
    cursor: pointer;
}

#DiaryBody table {
    width: 100%;
    border-collapse: collapse;
    -webkit-box-shadow: 3px 3px 5px 1px #bfbfbf;
            box-shadow: 3px 3px 5px 1px #bfbfbf;
}

#DiaryBody table td {
    padding: 10px 35px;
    border-top: thin solid #737373; 
    border-bottom: thin solid #a6a6a6;
}

#DiaryBody table td:first-child {
     border-left: thin solid #737373;
}

#DiaryBody table td:last-child {
     border-right: thin solid #737373;
}

#DiaryBody tr:last-child td {
    border-bottom: thin solid #737373;
}

.foodEntry a {
    cursor: pointer;
}

#deleteWrap {
    font-size: 23px;
}

.foodEntry #deleteWrap .fa {
    font-size: 31px;
    margin-right: 20px;
}

#deleteWrap a {
    cursor: pointer;
}

.itemName {
    font-size: 25px;
}

.itemName span {
    font-size: 20px;
}

.itemInfo {
    text-align: right;
    font-size: 21px;
}

.caloriesInfo {
    color: #ff8000;
}

.carbonInfo {
    color: #404040;
}

.waterInfo {
    color: #4dffff;
}


